.about_me
{
    position: relative;
    aspect-ratio: 1/1;
    display: flex;
    place-items: center;
   
}
.about_image
{
    width: 100%;
    height: auto;
    display: block;
    border-radius: 2rem;
    /* overflow: hidden; */
    transition: var(--transition);
    -webkit-transform: rotate(-20deg);
            transform: rotate(-20deg)  
}
.about_image1
{
   position: absolute;
    border-radius: 2rem;
    /* overflow: hidden; */
    transition: var(--transition);
    -webkit-transform: rotate(20deg);
            transform: rotate(20deg)  
    
}
.about_image:hover
{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    transition: var(--transition);
    z-index: 1;
    -webkit-animation:move 1s   forwards  ;
            animation:move 1s   forwards  ;
}
.about_image1:hover
{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    transition: var(--transition);
    z-index: 1;
    -webkit-animation: move 1s   forwards;
            animation: move 1s   forwards;
}
@-webkit-keyframes move {
    from {-webkit-transform: scale(1);transform: scale(1); }
    to {  -webkit-transform: scale(1.1);  transform: scale(1.1); }
  }
@keyframes move {
    from {-webkit-transform: scale(1);transform: scale(1); }
    to {  -webkit-transform: scale(1.1);  transform: scale(1.1); }
  }
.about_cards
{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 1.5rem;
    gap: 1.5rem;

}
.about_card
{
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem ;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}
.about_card:hover
{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;

}
.about_icon
{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;

}
.about_card h5
{
    font-size: 0.95rem;

}
.about_card small
{
    font-size: 0.7rem;
    color: var(--color-light);

}
.about_content p
{
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}
/*   Media queries for medium devices*/
@media screen and (max-width:1024px) {
    .about_container
    {
        grid-template-columns: 1fr;
        grid-gap: 0;
        gap: 0;

    }
    .about_me
    {
        width: 50%;
        margin: 2rem auto 4rem;
    }
    .about_content p
    {
        margin: 1rem 0 1.5rem;
    }

    
}
/*   Media queries for small devices*/
@media screen and (max-width:600px) {
 .about_me
 {
     width:65%;
     margin: 0 auto 3rem;
 }
.about_cards
{
    grid-template-columns: 1fr 1fr;
    grid-gap: 1rem;
    gap: 1rem;
}
.about_content{
    text-align: center;
}
.about_content p
{
    margin: 1.5rem 0;
}

}

nav
{
    background: rgba(0,0,0,0.3);
    width: -webkit-max-content;
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
}
nav a
{
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1 rem;
}
nav a:hover
{
    background: rgba(0,0,0, 0.3);
}
nav a:active
{
    background: rgb(15, 94, 190);
    color:var(--color-light);
}
.active
{
    background: rgb(15, 94, 190);
    color:var(--color-light);
}
header
{
    height: 110vh;
    padding-top: 7rem;
    overflow: hidden;
}
.header_container
{
    text-align: center;
    height: 100%;
    position: relative;
}
/* for cta  */



.cta
{
    margin-top: 2.5rem;
    display: flex;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    justify-content: center;
}

/* social  */
.header_social
{
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: absolute;
    left: 4rem;
    bottom: 15vh;   
}
.header_social::after
{
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

.me
{
    background-image:linear-gradient(var(--color-primary),transparent);
    width: 22rem;
    height: 30 rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 2rem;
    margin-bottom: 0%;
    border-radius: 50%;
    overflow: hidden;
     /* padding: 5rem 1.5rem 1.5rem 1.5rem; */
}

/* for scroll down */

.scroll_down
{
    position: absolute;
    right: 4rem;
    bottom: 28vh;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/*   Media queries for medium devices*/
@media screen and (max-width:1024px) {
    header
    {
        height: 110vh;

    }
    
}
/*   Media queries for small devices*/
@media screen and (max-width:600px) {
   header{ 
       height: 105vh;
   }
   .header_social,.scroll_down
   {
       display: none;
   }
}

